import Editor from '../tinymce/tinymce-vue'
export default {
	props:{
		value:String
	},
	components: {
	    'RichTextEditor': Editor
	},
	data() {
		return {
			WatchValue:this.value
		};
	},
	watch:{
		WatchValue(value){
			this.$emit('change',value)
		}
	}
}
